<template>
  <v-app id="inspire">
    <v-content>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="8" md="4">
            <v-card class="elevation-12">
              <v-toolbar color="primary" dark flat>
                <v-toolbar-title>Login form</v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>
              <p v-if="message">{{ message }}</p>
              <v-card-text>
                <v-form>
                  <v-text-field
                    label="Username"
                    name="username"
                    v-model="user.username"
                    prepend-icon="person"
                    type="text"
                  ></v-text-field>

                  <v-text-field
                    id="password"
                    label="Password"
                    name="password"
                    v-model="user.password"
                    prepend-icon="lock"
                    type="password"
                  ></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" type="submit" @click="doLogin(user)"
                  >Login</v-btn
                >
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {};
  },
  created() {},
  computed: {
    ...mapState({ user: "user", message: "message" }),
  },
  methods: {
    ...mapActions({ login: "login" }),
    doLogin() {
      this.login(this.user)
        .then((res) => {
          localStorage.setItem("token", res);
          this.$router.push("/home");
        })
        .catch((err) => {
          alert("Username && Pssword valid");
          console.log(err);
        });
    },
  },
};
</script>
